.arrowIcon {
  margin: 10px;
  cursor: pointer;
}

.arrowIcon:hover {
  color: var(--twitter-color);
}

.mainProfile {
  background-color: var(--twitter-background);
  margin-top: 0;
  border: 1 px solid white;

  .coverImageContainer {
    position: relative;

    img.coverImage {
      height: 200px;
      width: 600px;
    }

    .hoverCoverImage {
      position: absolute;
      top: 0%;
      background-color: rgba(0, 0, 0, 0.603);
      width: 100%;
      height: 98%;
      opacity: 0%;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        opacity: 100%;
      }
    }
  }
}

.photoIcon {
  color: white;
  font-size: 35px !important;
  opacity: 40%;
  margin-right: 20px;
  cursor: pointer;

  &:hover {
    opacity: 100%;
  }

  &.photoIconDisabled {
    cursor: not-allowed;
  }
}

.userInfo {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.heading-4 {
  margin-top: -40px;
  font-size: 19px;
  margin-left: 50px;
  margin-bottom: 10px;
  justify-content: space-between;
}

.heading-3 {
  margin-left: 20px;
  font-size: 24px;
}

.usernameSection {
  margin-left: 20px;
  color: rgba(251, 251, 251, 0.714);
}

.profile-bio {
  height: 100px;
}

.avatarContainer {
  margin-top: -80px;
  margin-left: 20px;
  border-radius: 100%;
  position: relative;
  height: 150px;
  width: 150px;
  border: 4px solid white;
}

.avatar {
  position: relative;
  width: 100%;
  height: 98%;
  border-radius: 100%;
}

.hoverAvatarImage {
  position: absolute;
  top: 0%;
  background-color: rgba(0, 0, 0, 0.603);
  width: 100%;
  height: 98%;
  border-radius: 100%;
  opacity: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hoverAvatarImage:hover {
  opacity: 100%;
}

.Tweet {
  background-color: white;
  width: 20%;
  height: 20%;
  padding: 2rem 1rem;
  color: skyblue;
}

.tweetsText {
  margin-top: 20px;
  margin-left: 20px;
  margin-bottom: 5px;
}

.infoContainer {
  margin-left: 20px;
  margin-top: 10px;
}

.locationAndLink {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 10px;
}

.subInfo {
  display: flex;
  align-items: center;
  gap: 5px;
}

.link {
  color: var(--twitter-color);
  cursor: pointer;

  &:hover {
    color: var(--twitter-color);
    background: rgba(255, 255, 255, 0.301);
  }
}

input {
  &#coverImage,
  &#profileImage {
    display: none;
  }
}
